body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.home-container {
  position: fixed;
  left: 10%;
  right: 10%;
  top: 50%;
  transform: translate(0, -50%);
  display: flex;
  flex-direction: column;
}

.text-input-field {
  padding: 24px 12px;
  border-radius: 7px;
  font-size: 24px;
}

.text-input-field:focus {
  outline: none;
}

.enter-room-button {
  margin-top: 20px;
  padding: 24px 12px;
  font-size: 28px;
  background-color: rgb(0, 132, 255);
  color: white;
  font-weight: 600;
  text-align: center;
  text-decoration: none;
  border-radius: 7px;
}

.chat-room-container {
  position: fixed;
  left: 0;
  right: 0;
  top: 33px;
  bottom: 0;
  display: flex;
  flex-direction: column;
  margin: 16px;
}

.chat-room-top-bar {
  display: flex;
  align-items: center;
}

.chat-room-top-bar h1 {
  flex: 1 1;
}

.messages-container {
  flex: 1 1;
  min-height: 100px;
  overflow: auto;
  border-radius: 7px 7px 0 0;
  margin-bottom: 8px;
}

.messages-list {
  list-style-type: none;
  padding: 0;
}
.avatar {
  vertical-align: middle;
  border-radius: 50%;

  height: 75px;
  width: 75px;
}

.message-item {
  display: flex;
  margin: 8px auto;
}

.my-message {
  justify-content: flex-end;
}

.received-message {
  justify-content: flex-start;
}

.message-body-container {
  max-width: 60%;
}

.message-body {
  flex: 1 1;
  border-radius: 4px;
  padding: 8px 12px;
  word-break: break-word;
}

.message-user-name {
  font-size: 0.75rem;
  padding-left: 12px;
  color: rgb(138, 141, 145);
}

.my-message .message-body {
  background-color: rgb(0, 132, 255);
  color: white;
}

.received-message .message-body {
  background-color: rgb(228, 230, 235);
  color: rgb(5, 5, 5);
}

.message-avatar-container {
  align-self: flex-end;
  margin: 0px 8px;
}

.message-avatar {
  vertical-align: middle;
  border-radius: 50%;

  height: 75px;
  width: 75px;
}

.new-message-form {
  display: flex;
  position: absolute;
  bottom: 0;
  width: 100%;
  margin-left: 15%;
}

.new-message-input-field {
  flex: 1 1;
  font-size: 1rem;
  padding: 8px 12px;
  resize: none;
  border-radius: 8px;
}

.send-message-button {
  font-size: 1.5rem;
  font-weight: 600;
  color: white;
  background: rgb(0, 132, 255);
  padding: 8px 12px;
  border: none;
  border-radius: 8px;
  margin-left: 10px;
}

.new-message-input-field,
.send-message-button {
  border-color: #9a9a9a;
}

.dotsContainer {
  width: 60px;
  height: 25px;
  background: #f2f2f2;
  border-radius: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
}

#dot1,
#dot2,
#dot3 {
  width: 7px;
  height: 7px;
  background: #cacaca;
  border-radius: 50%;
  margin: 3px;
  transition: all 0.5s ease-in-out;
  animation: typing 1s infinite;
}

#dot1 {
  animation-delay: 1s;
}

#dot2 {
  animation-delay: 0.5s;
}

#dot3 {
  animation-delay: 0.8s;
}

@keyframes typing {
  0% {
    transform: translateY(0);
    transition: all 0.5s ease-in-out;
  }

  50% {
    transform: translateY(-5px);
    transition: all 0.5s ease-in-out;
  }

  100% {
    transform: translateY(0);
    transition: all 0.5s ease-in-out;
  }
}

